.myProjectBlock{
    min-height: 100vh;
    padding: 50px 0;
}

.projectContainer{
    flex-direction: column;
    justify-content: flex-start;
}

.myProjects{
    /*border: 1px solid;*/
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    gap: 40px;
}

@media screen and (max-width: 320px ){
    .photo {
        margin: 0 0 30px 0;
        width: 250px;
        height: 350px;
        border-radius: 30px 0 30px 0 ;
        box-shadow: #00b074 12px 12px 10px;
    }
}
