.mainBlock {
    min-height: 90vh;
    background-color: #e2e2e2;
}

.text {
    overflow: hidden;
    width: 450px;
}

.photo {
    margin-top: 10px;
    width: auto;
    height: 550px;
    border: 1px #00b074 solid;
    padding: 20px;
    border-radius: 30px 0 30px 0 ;
    box-shadow: #00b074 12px 12px 10px;
}

.heading {
    font-size: 20px;
    color: #4d4d4d;
    opacity: 1;
    margin: 0 0 8px;
}

.name {
    font-size: 50px;
    text-transform: capitalize;
    font-weight: 900;
    margin-bottom: 15px;
    margin-top: 10px;
}

@media screen and (max-width: 500px ){
    .photo {
        margin: 0 0 10px 0;
        width: 300px;
        height: 450px;
        border-radius: 30px 0 30px 0 ;
        box-shadow: #00b074 12px 12px 10px;
    }
}

@media screen and (max-width: 320px ){
    .photo {
        margin: 0 0 30px 0;
        width: 250px;
        height: 350px;
        border-radius: 30px 0 30px 0 ;
        box-shadow: #00b074 12px 12px 10px;
    }
}