.navContainer {
    display: flex;
    flex-direction: row;
    width: 80%;
    height: 100%;
    margin: 0 auto;
    justify-content: end;
    background-color: #e2e2e2;
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 435px;
}

.navLink {
    color: #000;
    margin-right: 0;
    padding: 0;
    letter-spacing: 1px;
    position: relative;
    font-size: 16px;
    text-transform: uppercase;
    opacity: .5;
    font-weight: 600;
    text-decoration: none;
    font-family: "Open Sans", sans-serif;
    cursor: pointer;
}

.navLink:hover {
    color: #00b074;
}


@media screen and (max-width: 753px) {
    .nav {
        width: 400px;
    }
}