.imgContainer{
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 250px;
    justify-content: center;
    display: flex;
    align-items: center;
    border-radius: 10px 10px 0 0 ;
}

.project{
    border-radius: 10px;
    background: #00b074;
    width: 295px;
    /*background: #2b3940;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.description{
    color: white;
}

.link{
    background: #ffffff;
    align-items: center;
}

@media screen and (min-width: 1417px){
    .project{
        width: 400px;
    }
}

@media screen and (max-width: 320px ){
    .project{
        width: 100%;
    }
}
